import React, { Suspense } from 'react'
import { Spin } from 'antd';
import OperationType from '@/constant/OperationType'
import ConvertType from '@/constant/ConvertType'
import ConvertForms from '@/constant/config'
import activitiesCode from '@/axios/activitiesCode';
import record_activity from '@/axios/recordactivities'
import Fact from '@/model/fact'
import ToFactsheet from '@/components/Generate/ToFactsheet/ToFactsheet'
import ToDataComics from '@/components/Generate/ToDataComics/ToDataComics'
import ToDashboard from '@/components/Generate/ToDashboard/ToDashboard'
import ToDataVideos from '@/components/Generate/ToDataVideos/ToDataVideos'
import ToH5 from '@/components/Generate/ToH5/ToH5'
import Color from '@/constant/Color'
import ProgressBarView from '@/components/ProgressBar/ProgressBarView'
import config from '@/axios/config'
import * as api from '@/axios/api'
import './GeneratePannel.less';


export default class GeneratePannel extends React.Component {

    state = {
        operateState: this.props.operateState,
        convertingType: ConvertForms[0]
    }

    UNSAFE_componentWillReceiveProps(nextProps) {
        if (nextProps.operateState !== this.props.operateState) {
            this.setState({
                operateState: nextProps.operateState
            })
        }
    }

    componentWillUnmount(){
        this.setState=()=>false
    }
    componentDidMount(){
        if(this.state.operateState!==OperationType.PUBLISHED)
            this.props.history.push('/edit/datavideos')
        // this.clickGenerate("factsheet")
    }



    generate = (url,param) => {  //add param
        return new Promise(async (resolve, reject) => {
            let fileName = this.props.fileName;
            this.props.generateStory([], [], '');
            this.props.updateProgress(this.props.maxStoryLength, this.props.maxStoryLength);//reset progress bar
            //setTimeout 8s
            let max_iteration = 8,
                count = max_iteration;//inital

            let timer = setInterval(() => {
                if (count === 0) {
                    clearInterval(timer)
                }
                count--;
                this.props.updateProgress(max_iteration, count < 0 ? 0 : count);
            }, 1500)

            const { storyParameter } = this.props;

            let data ;
            if(param==='generate'){
                data= {
                    file_name: fileName,
                    max_story_length: storyParameter.maxStoryLength,
                    method: "mcts", // default: random
                }
            }
            else{
                let filters = _.cloneDeep(this.props.filters);
                this.props.updateFiltersChangeFlag(false)
                this.props.updateFiltersLocal(this.props.filters, this.props.filtersTitle, this.props.datares, this.props.schemaFilter) 
                data = {
                    file_name: fileName,
                    filters: JSON.stringify(filters),
                    max_story_length: storyParameter.maxStoryLength,
                    method: "mcts",
                }
                this.props.unusedFacts.map((d,i)=>{
                    this.props.deleteUnusedFact(0)
                })
              
            }
           
            const response = await api.generate(url, data)
            clearInterval(timer);
            if (response.data.fail) {
                this.setState({
                    operateState: OperationType.FAILED,
                    errorMessage: response.data.fail
                })
                reject()
                record_activity(activitiesCode.generateFactSheet, 0 , {filename:fileName, response:response});
            }
            if (response.data.error) {
                this.setState({
                    operateState: OperationType.FAILED,
                    errorMessage: response.data.error
                })
                reject()
                record_activity(activitiesCode.generateFactSheet, 0 , {filename:fileName, response:response});
            }
            this.props.updateProgress(max_iteration, 0);//100%
            this.setState({
                operateState: OperationType.GENERATED
            })
            resolve(response)
            record_activity(activitiesCode.generateFactSheet, 1 , {filename:fileName, response:response});
        })
    }

    getPannelClassName = () => {
        const { operateState } = this.state;
        switch (operateState) {
            case OperationType.UPLOADED:
                return "pannelWithConvertType whitePannel"
            case OperationType.GENERATING:
            case OperationType.GENERATED:
                return "draggerPannelCommon whitePannel"
            default:
                return "";
        }
    }

    isShowProgressbar = () => {
        const { operateState } = this.state
        if (operateState === OperationType.GENERATING) {
            return true;
        }
        return false;
    }
    getGenerateURL = (type) => {
        let url;
        switch (type) {
            case ConvertForms.DATACOMICS:
                url = config.url.generateGraphRandomly;
                break;
            case ConvertForms.FACTSHEET:
            default:
                url = config.url.generateRandomly;
                break;
        }
        return url;
    }

    clickGenerate = (typeForm,param = 'generate') => {
        this.setState({
            convertingType: typeForm,
            operateState: OperationType.GENERATING
        })

        let type = typeForm.name
        this.props.updateCovertType(type) //prepare download template
        this.generate(this.getGenerateURL(type),param).then(response => {
            const facts = response.data.story.facts;
            let tempFacts = [];
            switch (type) {
                case ConvertType.DATACOMICS:
                    for (let factDict of facts) {
                        let fact = new Fact(
                            factDict['type'],
                            factDict['measure'],
                            factDict['subspace'],
                            factDict['breakdown'],
                            factDict['focus'],
                            factDict['parameter'],
                            "", // chart
                            factDict['score'],
                            factDict['information'],
                            factDict['significance']
                        )
                        tempFacts.push(fact);
                    }

                    this.props.generateStory(tempFacts.slice(), [
                        "none",
                        "similarity",
                        "similarity",
                        "similarity",
                        "similarity",
                        "similarity"
                    ], 1);//todo:storystore
                    break;
                case ConvertType.FACTSHEET:
                case ConvertType.DASHBOARD:
                case ConvertType.DATAVIDEOS:
                    for (let factDict of facts) {
                        let fact = new Fact(
                            factDict['type'],
                            factDict['measure'],
                            factDict['subspace'],
                            factDict['groupby'],
                            factDict['focus'],
                            factDict['parameter'],
                            "", // chart
                            factDict['score'],
                            factDict['information'],
                            factDict['significance']
                        )
                        tempFacts.push(fact);
                    }

                    this.props.generateStory(tempFacts.slice(), [
                        "none",
                        "similarity",
                        "similarity",
                        "similarity",
                        "similarity",
                        "similarity"
                    ], 1);
                    break;

                case ConvertType.H5:
                    for (let factDict of facts) {
                        let fact = new Fact(
                            factDict['type'],
                            factDict['measure'],
                            factDict['subspace'],
                            factDict['groupby'],
                            factDict['focus'],
                            factDict['parameter'],
                            "", // chart
                            factDict['score'],
                            factDict['information'],
                            factDict['significance']
                        )
                        tempFacts.push(fact);
                    }
                    this.props.generateStory(tempFacts.slice(), [
                        "none",
                        "similarity",
                        "similarity",
                        "similarity",
                        "similarity",
                        "similarity"
                    ], 1);
                    break;

                default:
                    break;
            }

            switch (type) {
                case ConvertType.FACTSHEET:
                    this.props.history.push('/edit/factsheet')
                    break;
                case ConvertType.H5:
                    let newTab = window.open('about:blank');
                    api.shareStory(JSON.stringify({ ...this.props, lanh: 'EN' })).then(response => {
                        if (newTab) {
                            newTab.location.href = `lite.datacalliope.com/#/H5/${response.share_id}`
                        }
                        this.props.updateOperation(OperationType.PUBLISHED)
                    })
                    break;
                case ConvertType.DATAVIDEOS:
                    this.props.history.push('/edit/datavideos')
                    break;
                default:
                    break;
            }

        })
    }

    render() {
        const { intl, initDone, generateProgress } = this.props;
        const { operateState, convertingType } = this.state

        console.log("operateState", operateState);

        const ConvertingArrow = ({ operateState }) => {
            return (<div className="arrowLineDiv" >
                <p> {this.props.generateProgress + "%"}</p>
                <div className="arrowLine"></div>
                {
                    operateState === OperationType.GENERATED ?
                        <p> {initDone && intl.get("Success")}</p>
                        :
                        <p> {initDone && intl.get("Coverting")}</p>
                }
            </div>)
        }

        const FileNameView = ({ real_filename }) => {
            return <div className='CSVFileImage'>
                <div className='csvIconBlack'></div>
                <span className="fileName">{real_filename}</span>
            </div>
        }

        let PannelContentView;
        switch (operateState) {
            case OperationType.UPLOADED:
                PannelContentView = <>
                    <div style={{ height: "340px", flex: 1 }}>
                        <div className='pannel-box'>
                            <FileNameView real_filename={this.props.real_filename} />
                        </div>
                    </div>
                    <div className='ConvertFormsDiv' style={{ columnCount: Math.ceil(ConvertForms.length / 2) }}>
                        {
                            ConvertForms.map((type, idx) => {
                                return <div className='convert-box'
                                    key={idx}
                                    style={{ height: ConvertForms.length === 1 ? "100%" : "50%", backgroundColor: Color.CONVERT[idx] }}
                                    onClick={() => this.clickGenerate(type)}>
                                    <div className='icon'
                                        style={{ backgroundImage: `url(${type.iconUrl})` }}
                                    />
                                    <span>{initDone && intl.get(`To${type.name}`)}</span>
                                </div>
                            })
                        }
                        {
                            ConvertForms.length !== 1 && ConvertForms.length % 2 !== 0 ?
                                <div style={{ height: "50%", visibility: "none", backgroundColor: "transparent" }}></div>
                                : null
                        }
                    </div>
                </>
                break;
            case OperationType.GENERATING:
                PannelContentView = <div style={{ height: "340px", flex: 1 }}>
                    <div className='pannel-box'>
                        <div style={{ display: 'flex' }}>
                            <FileNameView real_filename={this.props.real_filename} />
                            <div className='VHCenter'>
                                <ConvertingArrow operateState={operateState} {...this.props} />
                                <div className='CSVFileImage'>
                                    <div className='GenerateIcon'
                                        style={{ backgroundImage: `url(${convertingType.generateIconUrl})` }}
                                    />
                                    <span className='fileName'>{initDone && intl.get(`To${convertingType.name}`)}</span>
                                </div>
                            </div>
                        </div>
                    </div>
                </div >
                break;
            case OperationType.PUBLISHED:
                const Loading = () => {
                    return (<div style={{ width: '100%', height: '100%', display: 'flex', justifyContent: 'center', marginTop: "50px" }}>
                        <Spin />
                    </div>)
                }
                console.log("OperationType.PUBLISHED", convertingType.name);
                //dynamic load the supported button view according to the convert type
                let GeneratedPannel;
                
                switch (convertingType.name) {
                    case ConvertType.FACTSHEET:
                        GeneratedPannel = <ToFactsheet reGnerate={this.clickGenerate}  {...this.props} />
                        break;
                    case ConvertType.DATACOMICS:
                        GeneratedPannel = <ToDataComics reGnerate={this.clickGenerate}  {...this.props} />
                        break;
                    case ConvertType.DASHBOARD:
                        GeneratedPannel = <ToDashboard reGnerate={this.clickGenerate}  {...this.props} />
                        break;
                    case ConvertType.DATAVIDEOS:
                        GeneratedPannel = <ToDataVideos reGnerate={this.clickGenerate}  {...this.props} />
                        break;
                    case ConvertType.H5:
                        GeneratedPannel = <ToH5 reGnerate={this.clickGenerate}  {...this.props} />
                        break;
                    default:
                        break;
                }

                PannelContentView = <div style={{ height: "340px", flex: 1 }}>
                    <div className='pannel-box'>
                        <div style={{ display: 'flex', flexDirection: "column", alignItems: "center", height: "100%" }}>
                            <Suspense fallback={<Loading />}>
                                {GeneratedPannel}
                            </Suspense>
                        </div>
                    </div>
                </div >
                break;
            default:
                break;
        }


        return <div className={this.getPannelClassName()}>
            <ProgressBarView percent={generateProgress} isActive={this.isShowProgressbar()} />
            {
                PannelContentView
            }
        </div>
    }
}