import React from 'react'
import ConvertForms from '@/constant/config'
import OperationType from '@/constant/OperationType'
import html2pdf from 'html2pdf.js'
import ButtonBoxView from '../Components/ButtonBox/ButtonBoxView'
import CommonSlotView from '../Components/commonSlot/CommonSlotView'
import AliCloud from '@/constant/imageUrl'
import activitiesCode from '@/axios/activitiesCode';
import record_activity from '@/axios/recordactivities';
import './ToFactsheet.less'
import '../common.less'


export default class ToFactsheet extends React.Component {

    state = {
        isDownloading: false
    }
    // download = () => {
    //     this.setState({
    //         isDownloading: true
    //     })
    //     //pdf
    //     let data = {

    //     }
    //     api.generatePDF(data).then(() => { })
    // }

    download = () => {
        console.log(this.props)
        this.setState({
            isDownloading: true
        })
        let PDFWoker;
        const element = document.getElementById('PDF');
        let _that = this;
        if (element) {
            const opt = {
                filename: this.props.fileName.split(".")[0],
                image: { type: 'jpeg', quality: 0.98 }, // 
                html2canvas: {
                    allowTaint: true,
                    //foreignObjectRendering: true,
                    onrendered: () => {
                        _that.setState({ operateState: OperationType.GENERATED });
                    },
                    width: 825,
                    height: 1166,
                    scrollX: 0,
                    scrollY: 0,
                    logging: true
                }
            };
            PDFWoker = html2pdf().set(opt).from(element);
            setTimeout(() => {
                try {
                    console.log("setTimeout outputPdf")
                    const opt2 = {
                        margin: 1,
                        jsPDF: { unit: 'in', orientation: 'portrait' },
                    };
                    PDFWoker.outputPdf(opt2).save();
                    this.setState({
                        isDownloading: false
                    })
                    record_activity(activitiesCode.downloadFile, 1, {filename:opt.filename})
                }
                catch (error) {
                    record_activity(activitiesCode.downloadFile, 0, {filename:opt.filename,error:error})
                }
            }, 3000)
        }
    }

    reUpload = () => {
        this.props.updateOperation(OperationType.BEFORE_UPLOAD)
    }
    reGnerate = () => {
        this.props.reGnerate(ConvertForms[0],'regenerate')
    }

    editPage = () => {
        this.props.history.goBack();
    }

    click = (text) => {
        const { intl, initDone } = this.props;
        switch (text) {
            case initDone && intl.get("Upload"):
                this.reUpload()
                break;
            case initDone && intl.get("Regnerate"):
                this.reGnerate()
                break;
            case initDone && intl.get("Download"):
                this.download()
                break;
            case initDone && intl.get("Edit"):
                this.editPage()
                break;
            default:
                break;
        }
    }

    render() {
        const { intl, initDone, fileName, real_filename } = this.props;
        const { isDownloading } = this.state
        //add your button here
        const supportedButton = [
            {
                iconUrl: `${AliCloud}/upload_white.png`,
                text: initDone && intl.get("Upload")
            },
            {
                iconUrl: `${AliCloud}/download/regenerate.png`,
                text: initDone && intl.get("Regnerate")
            },
            {
                iconUrl: `${AliCloud}/download/download.png`,
                text: initDone && intl.get("Download")
            },
            {
                iconUrl: `${AliCloud}/download/edit.png`,
                text: initDone && intl.get("Edit")
            }
        ]
        return (
            <CommonSlotView myClassName={"toFactsheet"} fileName={fileName} real_filename={real_filename} isSpining={isDownloading} columnCount={supportedButton.length} {...this.props}>
                {
                    supportedButton.map((button, index) => {
                        return <ButtonBoxView key={"index_" + index} iconUrl={button.iconUrl} text={button.text} onClickListener={() => this.click(button.text)} />
                    })
                }
            </CommonSlotView >
        )
    }
}